.user_nav {
  display: flex;
  flex-direction: column;
  gap: 12px;

  &_head {
    font-size: 12px;
    opacity: 0.5;
    border-bottom: 1px solid var(--on-element-color);
  }

  &_link {
    box-sizing: border-box;

    border-radius: 4px;
    text-decoration: none;
    color: var(--on-element-color);

    padding: 4px 12px;
  }
}

@media (hover: hover) and (pointer: fine) {
  .user_nav {
    &_link {
      &:hover {
        background-color: var(--hovered);
      }
    }
  }
}
