.profile {
  display: flex;
  position: relative;
  gap: 28px;

  &_aside {
    display: flex;
    flex-direction: column;
    gap: 28px;
    top: calc(28px + var(--nav-height));
    position: sticky;
  }

  &_main {
    display: flex;
    flex-direction: column;
    gap: 28px;
    position: relative;
    top: 28px;

    flex-basis: 75%;
  }

  &_left {
    position: relative;
    top: 28px;

    flex-basis: 25%;
  }
}